import Vue from 'vue'
import VueRouter from 'vue-router'
import show from "@/views/show/show.vue"
import index from "@/views/index/Index.vue"
import player from "@/views/video/videoPlay.vue"
import Videojs from 'video.js'
import VideojsZhcnLanguage from 'video.js/dist/lang/zh-CN.json'
import 'video.js/dist/video-js.css'
Videojs.addLanguage('zh-CN',VideojsZhcnLanguage)//设置中文
Vue.prototype.$videojs = Videojs
Vue.use(VueRouter)

const routes = [
  { path: "/", redirect: "index" },
  { path: "/index", component: index },
  { path: '/show/:category/:id', component: show },
  {path: "/video/:dir/:videoFileName", component: player }
]
//拒绝路由重复的问题
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

const router = new VueRouter({
  routes
})
export default router
