<template>
  <div class="video-container-in">
    <div class="video">
      <img
        ref="image"
        class="thumbnail"
        @mousemove="playVideo()"
        @mouseout="stopVideo()"
        @click="clickVideo()"
        alt
      />
      <div class="content">
        <div class="info">
          <h5 class="title">{{ product.videoInfo }}</h5>
          <p v-if="product.name != null" class="channel-name">
            {{ product.name }}
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["product"],
  mounted() {
    this.getImage();
  },
  updated() {
    this.getImage();
  },
  created() {},
  methods: {
    //透过ajax获取图片资源
    getImage() {
      let image = this.$refs.image;
      let url =
        this.localhost +
        "/product/image/getimage/" +
        this.product.dir +
        "/" +
        this.product.imageFileName;
      let request = new XMLHttpRequest();
      request.responseType = "blob";
      request.open("get", url, true);
      request.setRequestHeader(
        "Authorization",
        "Bearer " + this.Cookies.get(this.CookiesName)
      );
      request.onreadystatechange = (e) => {
        if (
          request.readyState == XMLHttpRequest.DONE &&
          request.status == 200
        ) {
          image.src = URL.createObjectURL(request.response);
          image.onload = () => {
            URL.revokeObjectURL(image.src);
          };
        }
      };
      request.send(null);
    },
    playVideo() {
      // console.log("play");
    },
    stopVideo() {
      // console.log("stop");
    },
    clickVideo() {
      this.$router.push(
        "/video/" + this.product.dir + "/" + this.product.videoFileName
      );
    },
  },
};
</script>

