<template>
  <div class="index-container">
    <h1></h1>
    <Top></Top>
    <Left></Left>
    <Filters :buttonList="catalogue"></Filters>

    <ul class="infinite-list" v-infinite-scroll="load" style="overflow: auto">
      <Main>
        <template v-for="(product, index) in videoList" slot="video">
          <AV :product="product" :key="index"></AV>
        </template>
      </Main>
    </ul>
  </div>
</template>

<script>
import Top from "@/views/index/top/top.vue";
import Left from "@/views/index/left/left.vue";
import Filters from "@/views/index/filter/filter.vue";
import Main from "@/views/index/mid/main.vue";
import AV from "@/views/index/mid/video/video.vue";
import eventBus from "../../api/common/bus.js";
export default {
  components: {
    Top,
    Left,
    Filters,
    Main,
    AV,
  },
  async mounted() {
    //查看是否有token
    let token = await this.Cookies.get("client1token");

    //根据dir and pageNumber get video信息
    this.$http
      .post("/product/index/getVideoInfo", { page: 1, dir: this.dir })
      .then((res) => {
        if (res.code == 200 && res.message == "ok") this.videoList = res.data;
      });
    //获取分类列表
    this.$http.post("/product/filter/getCatalogue", null).then((res) => {
      if (res.code == 200 && res.message == "ok") {
        this.catalogue = res.data;
      }
    });
    //当前dir一共多少页
    this.pageSize = await this.$http.post(
      "/product/index/indexTable/getPageNumber",
      { dir: this.dir }
    );
  },
  async created() {
    //点击更换按钮
    eventBus.$on("changeButton", async (dir) => {
      this.dir = dir;
      //获取页数
      this.pageSize = await this.$http.post(
        "/product/index/indexTable/getPageNumber",
        { dir: this.dir }
      );
      //获取列表
      this.videoList = (
        await this.$http.post("/product/index/getVideoInfo", {
          page: 1,
          dir: this.dir,
        })
      ).data;
      //设置页码
      this.currentPage = 1;
      if (this.videoList.length == 0) {
        this.$message.success("此栏目没有视频");
      }
    });

    eventBus.$on("searchButton", async (text) => {
      let data = await this.$http.post(
        "/product/index/indexTable/getSearchVideoInfo",
        { text: text }
      );
      if (data.code == 200) {
        if (data.data.length > 0) {
          this.currentPage = 1;
          this.pageSize = 1;
          this.videoList = data.data;
        } else {
          this.$message.success("没有相关的视频，请重新搜寻");
        }
      }
    });
  },
  async beforeMount() {},
  async dated() {},
  data() {
    return {
      videoList: null,
      catalogue: null,
      pageSize: 1,
      currentPage: 1,
      dir: "all",
    };
  },
  methods: {
    //滑动的时候触发
    async load() {
      if (this.currentPage == this.pageSize) {
        return;
      }
      this.currentPage++;
      let data = (
        await this.$http.post("/product/index/getVideoInfo", {
          page: this.currentPage,
          dir: this.dir,
        })
      ).data;
      let lenght = data.length;
      for (var i = 0; i < lenght; i++) {
        this.videoList.push(data[i]);
      }
    },
  },
};
</script>

<style lang="less" scoped>
.main-box {
  width: 100%;
  height: 100%;
}
.index-container {
  width: 100%;
  height: 100%;
  background-color: bisque;
  display: flex;
}
.top-bar {
  background-color: black;
  width: 100%;
  height: 5%;
}
</style>