<template>
  <div class="left-container">
    <!-- sidebar -->
    <div class="side-bar">
      <a
        v-for="item in leftBarList"
        :key="item.name"
        :class="currentId == item.name ? 'links active' : 'links'"
        @click="changeButton(item.name)"
      >
        <img :src="item.url" alt />
        {{ item.name }}
      </a>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      leftBarList: [
        { url: require("../../../assets/image/home.png"), name: "主页" },
        { url: require("../../../assets/image/explore.png"), name: "权限控制" },
        {
          url: require("../../../assets/image/subscription.png"),
          name: "管理员页面",
        },
        { url: require("../../../assets/image/Logout.png"), name: "登出" },
      ],
      currentId: "home",
    };
  },
  methods: {
   async changeButton(name) {
     if(name=="登出"){
       window.location.href="/logout";
       return false;
     }
      let flag= await this.havaAuthentication("admin");
      if(flag){
        if(name=="权限控制"){
          window.location.href=this.authrizationControl;
        }
        if(name=="管理员页面"){
          window.location.href=this.adminPage;
        }
        return false;
      }
      this.$message("你没有权限进入");
      return false;
    },
    //判断是否有权限
    async havaAuthentication(works) {
      let flag = false;
      let authorization = await this.Cookies.get("authCookie");
      let s = authorization.split("/");
      let length=s.length;
      for(var i =0;i<length;i++){
        if(works==s[i]) return true;
      }
      return false;
    }
  },
};
</script>

