import Vue from 'vue'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import App from './App.vue'
import router from './router'

// 导入并安装 Vant 组件库
import Vant from 'vant'
// 切记：为了能够覆盖默认的 less 变量，这里一定要把后缀名改为 .less
import 'vant/lib/index.less'

import http from './api/http';
import http2 from './api/http2';
import Cookies from 'js-cookie';
import Video from 'video.js'
import 'video.js/dist/video-js.css'
Vue.prototype.$video = Video


Vue.prototype.localhost="http://localhost:7001";
Vue.prototype.$http=http;
Vue.prototype.$http2=http2;
Vue.prototype.Cookies=Cookies;
Vue.prototype.CookiesName="client1token"
Vue.prototype.adminPage="http://localhost:8087";
Vue.prototype.authrizationControl="http://localhost:8090/auth/admin/adminPage";



Vue.use(Vant)
Vue.use(ElementUI);

Vue.config.productionTip = false;




new Vue({
  router,
  render: h => h(App)
}).$mount('#app')