<template>
  <div class="filter-container">
    <!-- filters -->
    <div class="filters">
      <button
        v-for="(item) in buttonList"
        :key="item"
        :class="currentIndex==item?'filter-options active':'filter-options'"
        @click="changeButton(item)"
      >{{item}}</button>
    </div>
  </div>
</template>

<script>
import eventBus from "../../../api/common/bus.js";
export default {
  props: {
    buttonList: {
      type: Array,
      default: function() {
        return [
          "all",
          "CSS",
          "web development",
          "python",
          "entertainment",
          "marvel",
          "javascript",
          "artificial intellience",
          "meachine leaning",
          "treand"
        ];
      }
    }
  },
  data() {
    return {
      currentIndex: "all",
    };
  },
  methods: {
    changeButton(value) {
      if(this.currentIndex==value) return;
      this.currentIndex = value;
      eventBus.$emit('changeButton',this.currentIndex);
    }
  },
  
};
</script>

<style lang="less" scoped>
/* filter */

.filters {
  position: fixed;
  left: 250px;
  top: 60px;
  width: calc(100% - 250px);
  height: 60px;
  background: #fff;
  border-top: 1px solid #dbdbdb;
  border-bottom: 1px solid #dbdbdb;
  padding: 0 20px;
  display: flex;
  align-items: center;
  overflow-x: auto;
  overflow-y: hidden;
}

.filters::-webkit-scrollbar {
  display: none;
}

.filter-options {
  flex: 0 0 auto;
  padding: 10px 20px;
  border-radius: 50px;
  background: #f0f0f0;
  border: 1px solid #dbdbdb;
  text-transform: capitalize;
  margin-right: 10px;
  color: #242424;
  font-size: 15px;
  cursor: pointer;
}

.filter-options.active {
  color: #fff;
  background: #242424;
}
</style>