<template>
  <div>
    <div>
      <div>
        <video
          id="video"
          class="video-js vjs-default-skin vjs-big-play-centered"
          controls
          preload="none"
        ></video>
      </div>
    </div>

    <!-- 图片缩图 -->
    <div class="demo-image" ref="image">
      <div class="block" v-for="fit in fits" :key="fit">
        <el-image
          style="width: 200px; height: 200px"
          :src="smallPic"
          :fit="fit"
        ></el-image>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
export default {
  components: {},
  data() {
    return {
      //videojs
      myPlayer: null,
      //vue
      that: null,
      //videodir
      dir: null,
      videoFileName: null,
      //videoinfo
      product: null,
      //img blob src
      videoImg: null,
      //video blob src
      videoSrc: null,
      //缩图的格式
      fits: ["fill"],
      //缩图的url
      smallPic: null,
    };
  },
  async mounted() {
    this.that = this;
    //vue 实例
    let that = this.that;
    this.dir = this.$route.params.dir;
    this.videoFileName = this.$route.params.videoFileName;
    //获取视频info
    this.product = (
      await this.$http.post("/product/video/info", {
        videoFileName: this.videoFileName,
      })
    ).data;
    //获取imagebuffer
    let imageBuffer = await this.$http.get(
      "/product/image/getimage/" +
        this.product.dir +
        "/" +
        this.product.imageFileName,
      null,
      { responseType: "blob" }
    );
    this.videoImg = URL.createObjectURL(imageBuffer);

    //获videobuffer
    let dir = this.product.dir;
    let fileName = this.product.videoFileName.split(".")[0];
    let videoInfoUrl = "product/video/info/" + dir + "/" + fileName;
    let videoInfo = await this.$http.get(videoInfoUrl, null);
    //播放器
    let ms = new MediaSource();
    this.videoSrc = URL.createObjectURL(ms);
    //视频解码信息
    let encode = videoInfo.mimeType + "; " + "codecs=" + videoInfo.codes;
    //播放器流
    let buffer = null;
    let canAdd = true;
    //buffer存储队列
    let bufferList = [];
    //提前多少秒获取数据
    let readSize = 60;

    //初始化video
    this.myPlayer = this.$videojs(
      "video",
      {
        //确定播放器是否具有用户可以与之交互的控件。没有控件，启动视频播放的唯一方法是使用autoplay属性或通过Player API。
        controls: true,
        //自动播放属性,muted:静音播放
        muted: false,
        autoplay: true,
        //建议浏览器是否应在<video>加载元素后立即开始下载视频数据。
        preload: "auto",
        //设置视频播放器的显示宽度（以像素为单位）
        width: "1280px",
        //设置视频播放器的显示高度（以像素为单位）
        height: "720px",
        // url
        poster: this.videoImg, // 封面图片
        sources: [
          {
            src: this.videoSrc,
            type: "video/mp4",
          },
        ],
        playbackRates: [0.5, 1, 1.5, 2, 3], //倍速播放∂
      },
      function onPlayerReady() {
        var player = this;
        //获取video对象
        let video = document.getElementById("video_html5_api");
        //显示缩图
        displaySmallPic();
        //左右快进快退
        video.onkeydown = function (e) {
          if (e.keyCode == 39) {
            let time = video.currentTime + 5;
            if (time >= videoInfo.videoTime)
              video.currentTime = videoInfo.videoTime - 1;
            else video.currentTime = time;
          }
          if (e.keyCode == 37) {
            let time = video.currentTime - 5;
            if (time < 0) video.currentTime = 0;
            else video.currentTime = time;
          }
        };

        ms.addEventListener("sourceopen", async function () {
          //buffer流
          buffer = ms.addSourceBuffer(encode);
          //buffer添加完毕的监听函数

          //buffer添加完成执行
          buffer.onupdateend = function () {
            if (bufferList.length == 0) {
              canAdd = true;
            } else addNextBuffer();
          };
          //添加首段buffer
          await addBuffer(dir, fileName, 0, buffer);
          await addBuffer(dir, fileName, 1, buffer);
        });
        function displaySmallPic() {
          //video
          var videoSource = player.player_.children_[0];

          // videojs element
          var root = $(videoSource).closest(".video-js");

          // progress control bar element
          var controlBar = $(".vjs-progress-control")[0];

          console.log(controlBar);

          let TIME = -1;

          let flag = false;
          let timer = null;
          //监听control bar
          controlBar.addEventListener("mousemove", async function (e) {
            //controlbar上的文字
            var timeBar = $(this).find(".vjs-mouse-display .vjs-time-tooltip");
            let time = timeBar.text();
            if (time == TIME || time == "") return;
            TIME = time;
            let image = that.$refs.image;
            image.style.display = "none";
            clearTimeout(timer);
            timer = setTimeout(function () {
              let time1 = setTimeFormality(time);
              let dir = that.product.dir;
              let videoFileName = that.product.videoFileName;
              videoFileName = videoFileName.split(".")[0];
              getImageBytesOnSpecifiedTime(
                dir,
                videoFileName,
                true,
                that,
                time1
              );
              moveSmallPic(that, e);
            }, 100);
          });

          controlBar.addEventListener("mouseleave", function () {
            let image = that.$refs.image;
            image.style.display = "none";
          });
        }

        //点击进度条
        this.on("seeking", async function () {
          let time = video.currentTime;
          let curBlock = false;
          let videoBuffer = video.buffered;
          for (let i = 0; i < videoBuffer.length; i++) {
            let start = videoBuffer.start(i);
            let end = videoBuffer.end(i);
            //  console.log("进度条事件：" + time + "block:" + i + "blockStart:" + start + " " + "blockEnd:" + end + "blockLength:" + videoBuffer.length);
            if (time >= start && time <= end) {
              curBlock = true;
            }
          }
          if (!curBlock) {
            await addBuffer(
              dir,
              fileName,
              Math.floor(time / readSize) + 1,
              buffer
            );
          }
        });
        //时间更变事件
        this.on("timeupdate", async function () {
          //播放完毕后回到0
          let time = video.currentTime;
          if (time > videoInfo.videoTime) {
            video.currentTime = 0;
            this.pause();
            return;
          }
          let nextBlock = false;
          let videoBuffer = video.buffered;
          let start = 0;
          let end = 0;
          for (let i = 0; i < videoBuffer.length; i++) {
            start = videoBuffer.start(i);
            end = videoBuffer.end(i);
            if (time <= end - readSize) {
              nextBlock = true;
            }
          }
          if (!nextBlock) {
            if (time + readSize <= videoInfo.videoTime) {
              await addBuffer(
                dir,
                fileName,
                Math.ceil((time + readSize) / readSize),
                buffer
              );
            }
          }
        });
      }
    );
    //添加组件在video
    let video = document.getElementById("video");
    let smallpic = $(".demo-image").remove()[0];
    video.append(smallpic);

    //添加下一个buffer
    function addNextBuffer() {
      buffer.appendBuffer(bufferList.shift());
    }

    function moveSmallPic(vue, e) {
      //image element
      let image = vue.$refs.image;

      var timeBar = $(".vjs-mouse-display .vjs-time-tooltip");
      var offset = timeBar.offset();
      // // modify image possition
      image.style.top = offset.top - 200 + "px";
      image.style.left = offset.left - 80 + "px";
      image.style.display = "block";
    }

    function addComponentInVideo(el1, el2) {}
    async function getImageBytesOnSpecifiedTime(
      dir,
      videoFileName,
      canSend,
      vue,
      time
    ) {
      let url =
        "/product/image/getImageBytesOnSpecifiedTime/" +
        dir +
        "/" +
        videoFileName +
        "/" +
        time +
        "";
      if (canSend) {
        let bloburl = await vue.$http.get(url, null, { responseType: "blob" });
        that.smallPic = URL.createObjectURL(bloburl);
      }
    }

    function setTimeFormality(time) {
      //09:08:20 to xxx
      var temp = null;
      // format: 09
      if (/^\d+$/.test(time)) {
        // re-format to: 0:0:09
        time = "0:0:" + time;
      }
      // format: 1:09
      else if (/^\d+:\d+$/.test(time)) {
        // re-format to: 0:1:09
        time = "0:" + time;
      }
      temp = time.split(":");
      // calculating to get seconds
      time = +temp[0] * 60 * 60 + +temp[1] * 60 + +temp[2] + "";
      if (time == "NaN") time = "0";
      return time;
    }

    //添加buffer入口
    async function addBuffer(dir, videoName, block, buffer) {
      //发送请求获取视频byte
      let data = await readBlock(dir, videoName, block);
      //判断队列是否为空，空直接添加，不为空添加到队列中
      if (canAdd) {
        canAdd = false;
        buffer.appendBuffer(data);
      } else {
        bufferList.push(data);
      }
    }
    //发送请求
    async function readBlock(dir, videoName, block) {
      let url =
        "/product/video/getVideoBytes/" + dir + "/" + videoName + "/" + block;
      let data = await that.$http.get(url, null, {
        responseType: "arraybuffer",
      });
      return data;
    }
  },
  destroyed () {
    //销毁videojs
    this.myPlayer.dispose()
}
};
</script>

<style lang="less" scoped>
.vjs-control-bar .vjs-thumbnail {
  position: absolute;
  width: 158px;
  height: 90px;
  top: -100px;
  background-color: #000;
  display: none;
}
.demo-image {
  width: 200px;
  height: 200px;
  position: absolute;
  display: none;
  z-index: 100000000 !important;
}
</style>